import React, { Component } from 'react';
import { ROUTES } from '../../../constants/constants.ts';
import {
  CardInterface,
  CardsInterface,
  CategoryInterface,
} from '../../api/api'; // eslint-disable-line import/extensions
import './AdminPage.css';

interface AdminPageProps {
  cards: CardsInterface;
  categories: CategoryInterface[];
  disableAdminMode: () => void;
}

interface AdminPageState {
  newWordCard: CardsInterface | null;
  newCategoryCard: CategoryInterface | null;
  updatingCardIndex: number;
  isCreateCardMode: boolean;
  activeCategory: string;
}

class AdminPage extends Component<AdminPageProps, AdminPageState> {
  constructor(props: AdminPageProps) {
    super(props);
    this.state = {
      newWordCard: null,
      newCategoryCard: null,
      updatingCardIndex: -1,
      isCreateCardMode: false,
      activeCategory: '',
    };
  }

  setUpdatingCardIndex = (index: number): void => {
    this.disableCardCreatingState();
    this.setState({ updatingCardIndex: index });
  }

  resetUpdatingCardIndex = (): void => {
    this.setState({ updatingCardIndex: -1 });
  }

  enableCardCreatingState = (): void => {
    this.resetUpdatingCardIndex();
    this.setState({ isCreateCardMode: true });
  }

  disableCardCreatingState = (): void => {
    this.setState({ isCreateCardMode: false });
  }

  resetAllEditingCardsState = (): void => {
    this.resetUpdatingCardIndex();
    this.disableCardCreatingState();
  }

  createCategoryCard = (): void => {
    const { newCategoryCard } = this.state;
    this.resetAllEditingCardsState();
    console.log('create categoryCard', newCategoryCard);
  }

  createWordCard = (): void => {
    const { newWordCard } = this.state;
    this.resetAllEditingCardsState();
    console.log('create card', newWordCard);
  }

  updateCategoryCard = (categoryCard: CategoryInterface): void => {
    this.resetAllEditingCardsState();
    console.log('update categoryCard', categoryCard.name);
  }

  updateWordCard = (card: CardInterface): void => {
    this.resetAllEditingCardsState();
    console.log('update card', card.word);
  }

  deleteCategoryCard = (categoryCard: CategoryInterface): void => {
    this.resetAllEditingCardsState();
    console.log('delete categoryCard', categoryCard.name);
  }

  deleteWordCard = (card: CardInterface): void => {
    this.resetAllEditingCardsState();
    console.log('delete card', card.word);
  }

  getRoutePage = (route?: string): JSX.Element => {
    const { categories, cards } = this.props;
    if (route) {
      return (
        <>
          <h2 className="page-title">
            {`Admin Page - ${route} category words`}
          </h2>
          <a
            className="admin-page__btn categories-route-btn"
            href={`#/${ROUTES.adminPage}`}
            onClick={() => { this.setActiveCategory(); }}
            type="button"
          >
            back to categories
          </a>
          <ul className="admin-page__container cards-page-container">
            {cards[route].map((card: CardInterface, index: number): JSX.Element => (
              this.renderWordCard(card, index)
            ))}
            {this.renderWordCardCreateBtn()}
          </ul>
        </>
      );
    }
    return (
      <>
        <h2 className="page-title">
          Admin Page - categories
        </h2>
        <ul className="admin-page__container cards-page-container">
          {categories.map((card: CategoryInterface, index: number): JSX.Element => (
            this.renderCategoryCard(card, index)
          ))}
          {this.renderCategoryCardCreateBtn()}
        </ul>
      </>
    );
  }

  setActiveCategory = (category = ''): void => {
    this.resetAllEditingCardsState();
    this.setState({ activeCategory: category });
  }

  renderCategoryCard = (card: CategoryInterface, index: number): JSX.Element => {
    const { updatingCardIndex } = this.state;
    return (
      <li
        key={card.name}
        className="admin-page__card-container card-container"
      >
        <div className="admin-page__card card">
          {updatingCardIndex === index
            ? this.renderCategoryCardBack(card)
            : this.renderCategoryCardFront(card, index)}
        </div>
      </li>
    );
  };

  renderCategoryCardCreateBtn = (): JSX.Element => {
    const { isCreateCardMode: isCardCreating } = this.state;
    return (
      <li
        key="create"
        className="admin-page__card-container card-container"
      >
        <div className="admin-page__card card">
          {isCardCreating
            ? this.renderCategoryCardBack()
            : (
              <>
                <div className="admin-page__input-tip">Create New Category</div>
                <button
                  className="admin-page__btn add-category-btn"
                  onClick={this.enableCardCreatingState}
                  type="button"
                >
                  +
                </button>
              </>
            )}
        </div>
      </li>
    );
  }

  renderCategoryCardFront = (card: CategoryInterface, index: number): JSX.Element => {
    const { cards } = this.props;
    return (
      <div className="admin-page__card-front card-front">
        <button
          className="admin-page__delete-btn"
          onClick={() => this.deleteCategoryCard(card)}
          type="button"
          aria-label="delete"
        />
        <h4 className="admin-page__card-title card-title">{card.name}</h4>
        <div className="admin-page__card-text">
          Words:
          <div className="admin-page__card-text card-data-value">{cards[card.name].length}</div>
        </div>
        <div className="admin-page__btn-container">
          <button
            className="admin-page__btn update-btn"
            onClick={() => this.setUpdatingCardIndex(index)}
            type="button"
          >
            update
          </button>
          <a
            className="admin-page__btn words-route-btn"
            href={`#/${ROUTES.adminPage}/${card.name} words`}
            onClick={() => this.setActiveCategory(card.name)}
            type="button"
          >
            add word
          </a>
        </div>
      </div>
    );
  }

  renderCategoryCardBack = (card?: CategoryInterface): JSX.Element => (
    <div className="admin-page__card-back card-back">
      <div className="admin-page__input-tip">Category Name</div>
      <input
        type="text"
        className="admin-page__input"
        placeholder="category name"
        defaultValue={card?.name || ''}
        autoComplete="off"
      />
      <div className="admin-page__btn-container">
        <button
          className="admin-page__btn cancel-btn"
          onClick={this.resetAllEditingCardsState}
          type="button"
        >
          cancel
        </button>
        <button
          className="admin-page__btn submit-btn"
          onClick={() => (card ? this.updateCategoryCard(card) : this.createCategoryCard())}
          type="button"
        >
          save
        </button>
      </div>
    </div>
  )

  renderWordCard = (card: CardInterface, index: number): JSX.Element => {
    const { updatingCardIndex } = this.state;
    return (
      <li
        key={card.word}
        className="admin-page__card-container card-container admin-page__card-container--words"
      >
        <form className="admin-page__card card">
          {updatingCardIndex === index
            ? this.renderWordCardBack(card)
            : this.renderWordCardFront(card, index)}
        </form>
      </li>
    );
  }

  renderWordCardCreateBtn = (): JSX.Element => {
    const { isCreateCardMode: isCardCreating } = this.state;
    return (
      <li
        key="create"
        className="admin-page__card-container card-container admin-page__card-container--words"
      >
        <div className="admin-page__card card">
          {isCardCreating
            ? this.renderWordCardBack()
            : (
              <>
                <div className="admin-page__input-tip">Add New Word</div>
                <button
                  className="admin-page__btn add-word-btn"
                  onClick={this.enableCardCreatingState}
                  type="button"
                >
                  +
                </button>
              </>
            )}
        </div>
      </li>
    );
  }

  renderWordCardFront = (card: CardInterface, index: number): JSX.Element => (
    <div className="admin-page__card-front card-front">
      <button
        className="admin-page__delete-btn"
        onClick={() => this.deleteWordCard(card)}
        type="button"
        aria-label="delete"
      />
      <div className="admin-page__card-text admin-page__card-text--words">
        Word:
        <div className="admin-page__card-text card-data-value">{card.word}</div>
      </div>
      <div className="admin-page__card-text admin-page__card-text--words">
        Translation:
        <div className="admin-page__card-text card-data-value">{card.translation}</div>
      </div>
      <div className="admin-page__card-text admin-page__card-text--words">
        Audio:
        <div className="admin-page__card-text card-data-value">{card.audioSrc}</div>
      </div>
      {/* <button onClick={() => (this.playAudio(card.audioSrc))}>play audio</button> */}
      <div className="admin-page__card-text admin-page__card-text--words">Image:</div>
      <div className="admin-page__image" style={{ backgroundImage: `url(${card.image})` }} />
      <button
        className="admin-page__btn update-btn--words"
        onClick={() => this.setUpdatingCardIndex(index)}
        type="button"
      >
        update
      </button>
    </div>
  )

  renderWordCardBack = (card?: CardInterface): JSX.Element => (
    <div className="admin-page__card-back card-back">
      <div>
        <label htmlFor="audio" className="admin-page__input-tip">
          Word:
          <input
            type="text"
            className="admin-page__input"
            placeholder="word"
            defaultValue={card?.word || ''}
            autoComplete="off"
          />
        </label>
      </div>
      <div>
        <label htmlFor="audio" className="admin-page__input-tip">
          Translation:
          <input
            type="text"
            className="admin-page__input"
            placeholder="translate"
            defaultValue={card?.translation || ''}
            autoComplete="off"
          />
        </label>
      </div>
      <div className="admin-page__file-input">
        <label htmlFor="audio">
          Audio file:
          <input type="file" id="audio" name="audio" className="card-data-value" />
        </label>
      </div>
      <div className="admin-page__file-input">
        <label htmlFor="image">
          Image file:
          <input type="file" id="image" name="image" className="card-data-value" />
        </label>
      </div>
      <div className="admin-page__btn-container">
        <button
          className="admin-page__btn cancel-btn"
          onClick={this.resetAllEditingCardsState}
          type="button"
        >
          cancel
        </button>
        <button
          className="admin-page__btn submit-btn"
          onClick={() => (card ? this.updateWordCard(card) : this.createWordCard())}
          type="submit"
        >
          save
        </button>
      </div>
    </div>
  )

  renderAdminHeader = (): JSX.Element => {
    const { disableAdminMode } = this.props;
    const { activeCategory } = this.state;
    return (
      <header className="admin-header header">
        <div className="admin-header__container header__container">
          <div className="admin-header__link-container">
            <a
              className={`admin-header__link ${activeCategory ? '' : 'active'}`}
              href={`#/${ROUTES.adminPage}`}
              onClick={() => { this.setActiveCategory(); }}
              type="button"
            >
              Categories
            </a>
            <div className={`admin-header__link ${activeCategory ? 'active' : ''}`}>Words</div>
          </div>
          <a
            className="admin-header__link log-out-btn"
            href={`#/${ROUTES.mainPage}`}
            onClick={() => disableAdminMode()}
          >
            Log out
          </a>
        </div>
      </header>
    );
  }

  render(): JSX.Element {
    const { activeCategory } = this.state;
    return (
      <>
        {this.renderAdminHeader()}
        {activeCategory ? this.getRoutePage(activeCategory) : this.getRoutePage()}
      </>
    );
  }
}

export default AdminPage;
