import React from 'react';
import { ROUTES } from '../../../constants/constants.ts';
import './PopUp.css';

interface PopUpProps {
  onClickCancelBtn(): void;
  onClickSubmitBtn(): void;
}

const PopUp: React.FC<PopUpProps> = (props: PopUpProps) => (
  <div className="pop-up autorization-pop-up">
    <h3 className="pop-up__title">Login</h3>
    <form className="autorization-form">
      <input
        type="text"
        className="form__input"
        placeholder="login"
        autoComplete="off"
      />
      <input
        type="password"
        className="form__input"
        placeholder="password"
        autoComplete="off"
      />
      <div className="pop-up__btn-container">
        <button
          className="pop-up__btn cancel-btn"
          onClick={() => { props.onClickCancelBtn(); }}
          type="button"
        >
          cancel
        </button>
        <a
          className="pop-up__btn submit-btn"
          onClick={() => { props.onClickSubmitBtn(); }}
          href={`#/${ROUTES.adminPage}`}
        >
          submit
        </a>
      </div>
    </form>
  </div>
);

export default PopUp;
